
* {
  margin : 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #d2dbdd;
}

.heading {
 padding : 30px 0;
 text-align: center;
 text-transform:uppercase;
 letter-spacing: 2px;
 font-weight: 500;
 color: #868686;
 background-color: #fff;
 font-size: 1.5rem;
margin-bottom: 30px;
}

.cards {
  width: 100%;
  height: auto;
}

.card {
  margin: 6%;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);
  background-color: #fff;
  width: 21.25%;
  border-radius: 12px;
  box-shadow: 0px, 13px, 10px, -7px rgba(0, 0, 0, 0.1);
  float: left;
}

.card:hover{
  box-shadow: 0px, 30px, 18px, -8px rgba(0, 0, 0, 0.1);
  transform: scale(1.05, 1.05);
}

.card_img{
  width: 100%;
  height: 235px;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}

.card_info {
  background-color: #fff;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
  padding: 16px 24px 24px 24px;
}

.card_title{
  padding:5px ;
  font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  text-transform: uppercase;
  font-size: 14px;
  letter-spacing: 2px;
  font-weight: 800;
  color: #1f1919;
  background-color: #fff;
}

.card_category{
  font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: #868686;
  background-color: #fff;
}

button{
  padding: 5px 8px;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: #868686;
  background-color: #fff;
  outline: none;
  border: 1px solid black;
  cursor: pointer;
}

